import { useState } from "react";
import Group from "./Group";
import "./Groups.css";

const Groups = (props) => {
  const [currentGroup, setCurrentGroup] = useState(props.currentGroup);
  const moveHandler = (position) => {
    document.querySelector(
      ".groups-container"
    ).style.transform = `translateX(-${100 * position}%)`;
    setCurrentGroup(currentGroup + 1);
  };

  return (
    <div className="groups overflow-hidden">
      <div className="groups-container flex flex-nowrap flex-row groups-container-transition">
        {props.groupsData.map((group, i) => (
          <Group
            teams={group.teams}
            group={group.group}
            key={group.group}
            position={i}
            onMove={moveHandler}
            onResult={props.onResult}
          />
        ))}
      </div>
    </div>
  );
};

export default Groups;
